<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  inject: ['reload'],
  data () {
    return {
      columns: [
          {
            type: 'selection',
            align: 'center',
            width: 60
          },
          {
            title: 'ID',
            key: 'id',
            align: 'center',
            width: 120
          },
          {
            title: '游戏名称',
            key: 'name',
            align: 'center',
          },
          {
            title: '添加时间',
            key: 'formatTime',
            align: 'center',
          },
          {
            title: '图片',
            key: 'image_url',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('img', {
                  domProps: { // 修改标签默认内容
                    innerHTML: ""
                  },
                  attrs: {
                    src: params.row.image_url
                  },
                  style: {
                    margin: '5px 5px 0 5px',
                    maxWidth: '260px',
                    maxHeight: '150px',
                  },
                  on: {
                    click: () => {
                      this.previewImg(params);
                    }
                  }
                }, 'View')
              ]);
            }
          },
          {
            title: '状态',
            key: 'status_name',
            align: 'center',
            width: 160,
            render: (h, params) => {
              const status = params.row.status;
              const color = status == 1 ? 'success' : status == 2 ? 'default' : status == 3 ? 'warning' : 'error';
              const text = status == 1 ? '已发布' : status == 2 ? '已隐藏' : status == 3 ? '审核中' : '已退回';

              return h('Button', {
                props: {
                  type: color,
                  border: '1px solid #ccc',
                  size: 'small'
                },
                on: {
                  click: () =>{
                    const token = getToken(),
                          id = params.row.id,
                          status = params.row.status,
                          type = 'img',
                          p = {token, id, status, type};

                    api.ajaxUpdate(p).then(res => {
                      if(res.code == 0) {
                        this.$Message.success('修改成功')
                        this.reload()
                      } else {
                        this.$Message.error(res.msg)
                      }
                    })
                  }
                },
              }, text);
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                  h('Button', {
                    props: {
                      type: 'error',
                      size: 'small'
                    },
                    on: {
                      click: () =>{
                        console.log(params)
                        this.delImg(params.row.id)
                      }
                    },
                  }, '删除'),
              ]);
            }
          }
      ],
      data: [],
      formData: {
        name: '',
        status: '0',
      },
      configData: {},
      totalPages: 0,
      isLoading: true,
      page: 1,
      per_page: 10,
      keywordList:[],
      selectedGameId: '',
      isSearchFGame: false,//游戏名称自动填充搜索判断
    }
  },
  mounted () {
    let _this = this;
    this.formData.name = this.$route.params.name || '';
    _this.handleForm();
    
    document.onkeydown = function () {// 监听enter事件
      let key = window.event.keyCode;
      if (key === 13) { 
        _this.handleForm()
      }
    }
  },
  methods: {
    handleForm (flag) {
      flag ? this.page = 1 : null;
      const token = getToken(),
            page = this.page*1,
            per_page = this.per_page*1,
            name = this.formData.name,
            status = this.formData.status*1,
            params = {token, page, per_page, name, status};

      this.getData(params)
    },
    getData (params) {
      this.isLoading = true;
      api.gameImg(params).then(res => {
        this.isLoading = false
        if(res.code == 0) {
          this.totalPages = res.data.totlerows
          this.data = res.data.imglist
          this.configData = res.data.config
          this.data.forEach(ele => {
            //格式化时间
            ele.formatTime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
          })
        } else {
          this.$Notice.error({
           desc: res.msg
          })
        }
      })
    },
    delImgList () {
      if(this.selectedGameId) {
        this.$Modal.confirm({
          title: '提示',
          content: `<p style="word-break:break-all;">确定删除ID为：<span style="color:red">${this.selectedGameId}</span> 的截图么？</p>`,
          onOk: () => {
            const token = getToken(),
                  delid = this.selectedGameId,
                  type = 'img',
                  params = {token, delid, type};
            console.log(params)
            api.batchDel(params).then(res => {
              if(res.code == 0) {
                this.selectedGameId = '';
                this.$Notice.success({
                  title: res.msg
                })
                this.handleForm();
              }else {
                this.$Notice.error({
                  title: res.msg
                })
              }
            })
          },
          onCancel: () => {}
        });
      } else {
        this.$Message.warning('请勾选需要删除的截图')
      }
    },
    delImg (imgId) {
      console.log(imgId)
      this.$Modal.confirm({
        title: '提示',
        content: `<p style="word-break:break-all;">确定删除ID为：<span style="color:red">${imgId}</span> 的截图么？</p>`,
        onOk: () => {
          const token = getToken(),
                id = imgId,
                type = 'img',
                params = {token, id, type};
          console.log(params)
          api.ajaxDelete(params).then(res => {
            if(res.code == 0) {
              this.$Notice.success({
                title: res.msg
              })
              this.handleForm();
            }else {
              this.$Notice.error({
                title: res.msg
              })
            }
          })
        },
        onCancel: () => {}
      });
    },
    autoKeyword (query) {
      if(query != '') {
        const token = getToken(),
            name = this.formData.name,
            params = {token, name};
         
        api.backGameList(params).then(res => {
          if(res.code == 0) {
            if(res.data.length>0) {
              this.keywordList = res.data
              this.isSearchFGame = true;
            } else {
              this.isSearchFGame = false;
            }
          }else {
            this.isSearchFGame = false;
            this.$Notice.error({
              desc: res.msg
            })
          }
        })
      }
    },
    changePage (page) {
      this.page = page;
      this.handleForm();
    },
    changePageNum (num) {
      this.page = 1;
      this.per_page = num;
      this.handleForm();
    },
    selectGame (selection) {
      this.selectedGameId = '';
      if(selection.length>0) {
        selection.forEach(ele => {
          this.selectedGameId += (','+ele.id)
        })
      }else {
        this.selectedGameId = ''
      }
    },
    checkGame () {
      this.isSearchFGame = false
    },//主库游戏名称校验
    setKeyword (name) {
      this.formData.name = name
    },//主库游戏名称选中
  }
}
</script>
<template>
  <div>
    <h3><Icon type="md-barcode" size=24 />游戏截图列表</h3>
    <div class="formBox">
      <Button type="info" style="width:100px" to='/data/editImg'><Icon type="md-cloud-upload" size=14 />添加截图</Button>
      <Button type="error" @click="delImgList"><Icon type="ios-cut" />删除选中</Button>
      <div class="nameBox">
        <Input
          clearable
          v-model="formData.name"
          @on-change="autoKeyword"
          @on-blur="checkGame"
          placeholder="请在此输入游戏名称"
        />
        <ul v-show="isSearchFGame">
          <li 
            v-for="item,idx in keywordList" 
            :value="item" 
            :key="idx"
            @mousedown="setKeyword(item)"
            >{{ item }}</li>
        </ul>
      </div> 
      <Select v-model="formData.status" style="width:100px" @on-change="handleForm(true)">
        <Option value="0">游戏状态</Option>
        <Option v-for="itm,idx in configData.status" :value="idx" :key="idx">{{itm}}</Option>
      </Select>
      <Button type="primary" style="width:100px" @click="handleForm">
        <Icon type="ios-search" size=16></Icon>
        搜索
      </Button>
    </div>
    <Table 
      border 
      ref="selection" 
      :columns="columns" 
      :data="data" 
      :loading=isLoading
      @on-selection-change="selectGame"
      >
    </Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" :current='page' show-total show-elevator show-sizer :page-size=10 @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .formBox {
    .ivu-select {
      margin: 0 20px 20px 0;
    }
    .ivu-input-wrapper {
      margin: 0 20px 20px 0;
    }
    .ivu-date-picker, .ivu-btn {
      position: relative;
      vertical-align: middle;
      line-height: normal;
      margin: 0 20px 20px 0;
    }
    .ivu-select{
      width: 160px;
    }
  }
  .nameBox {
      display: inline-block;
      position: relative;
      margin-right: 20px;
      ul {
        min-width: 180px;
        max-height: 200px;
        position: absolute;
        will-change: top, left;
        transform-origin: center top;
        top: 33px;
        left: 0px;
        list-style: none;
        overflow: auto;
        margin: 5px 0;
        padding: 5px 0;
        background-color: #fff;
        box-sizing: border-box;
        border-radius: 4px;
        box-shadow: 0 1px 6px #00000020;
        z-index: 900;
        li {
          width: 180px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          line-height: 32px;
          text-indent: 8px;
          margin: 0;
          clear: both;
          color: #515a6e;
          font-size: 12px!important;
          white-space: nowrap;
          list-style: none;
          cursor: pointer;
          transition: all .2s ease-in-out;
          &:hover {
            background: #dedede99;
          }
        }
      }
      span {
        width: 110px;
        position: absolute;
        color: green;
        margin-left: 6px;
        &.err2 {
          color: red;
        }
      }
      .addGame {
        position: absolute;
        margin-left: 100px;
      }
  }
</style>
